 
 import React,{useState} from 'react';
import '../App.css';
import Board from './Board';
import Card from './Card';



function ButtonFill(){

    
 const [show, setShow] = useState(false);
    function handleClick() {
        setShow(!show);
        console.log(show);
        
      }
 

             
  return(
    
    <div className="buttonFill">
   <button className="button" onClick={handleClick} >Change</button>
   <p>este{show}</p>
   {show ? 'bla' : 'fara bla'}
   

   <Board id="board-2" className="board" title="Frameworks" >
    <Card id="card-2" className="card" draggable="true" bounds="parent" >
    <p >React</p>  
    </Card>
    <Card id="card-9" className="card" draggable="true" bounds="parent">
    <p>Laravel</p>  
    </Card>

    </Board>
    </div>
   
  );
}

export default ButtonFill;
 
 
 
 
  
